html {
  font-size: 16px;
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

h3 {
  font-size: 2rem;
  color: #0078B3;
  margin: 0;
}

p {
  margin: 0;
}

.header {
  margin-top: 1.42rem;
}

.navbar {
  display: flex;
  height: 236px;
  margin-left: 1.05rem;
}
.navbar .navbar__bar {
  width: 0.25rem;
  border-radius: 14px;
  color: black;
  background-color: #E9EBEE;
}
@media (min-width: 768px) {
  .navbar {
    display: flex;
    flex-direction: column-reverse;
    margin: 0;
    padding: 0;
  }
  .navbar .navbar__bar {
    margin: 0;
    padding: 0;
    width: 100%;
    height: 15px;
  }
}
.navbar .navbar__list {
  list-style: none;
  padding: 0;
  margin: 0;
}
@media (min-width: 768px) {
  .navbar .navbar__list {
    display: flex;
  }
}
.navbar .navbar__list .list__item {
  margin: 1rem 0;
  display: flex;
  flex-direction: row-reverse;
}
@media (min-width: 768px) {
  .navbar .navbar__list .list__item {
    display: flex;
    flex-direction: column;
    margin: 0;
  }
  .navbar .navbar__list .list__item .list__bar {
    margin-top: 2rem;
    background-color: red;
    color: red;
  }
}
.navbar .navbar__list .list__item:first-child {
  margin-top: 0;
}
.navbar .navbar__list .list__item:last-child {
  margin-bottom: 0;
}
.navbar .navbar__list .list__item .list__link {
  margin-left: 0.81rem;
  text-decoration: none;
  color: #0078B3;
  line-height: 1.25rem;
  letter-spacing: 0.8px;
  font-size: 0.9rem;
  font-weight: bold;
}
.navbar .navbar__list .list__item .list__link--active {
  color: #0D1C3D;
}
.navbar .navbar__list .list__bar {
  width: 0.25rem;
  border-radius: 14px;
  color: black;
  background-color: #E9EBEE;
  margin-left: -0.25rem;
}
.navbar .navbar__list .list__bar--active {
  background-color: #0D1C3D;
}

.container {
  width: 95%;
  display: none;
}
.container--active {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 0.5fr 2fr 3fr;
  row-gap: 1rem;
  align-items: center;
  justify-content: center;
}
.container--active .container__text {
  width: 100%;
  height: 95%;
  font-size: 0.875rem;
  line-height: 1.25rem;
}
.container--active .container__image {
  height: 100%;
  grid-row: 2/3;
}

.img .img__picture > img {
  display: block;
  height: auto;
  max-width: 100%;
  width: 100%;
}